body {
  font-family: Arial, sans-serif;
}

.loader {
  color: #ff4500;
  box-sizing: border-box;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 28px;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  width: 5px;
  height: 5px;
  box-sizing: border-box;
  background: currentColor;
  animation: animloader .5s linear infinite;
  position: absolute;
  bottom: 8px;
  right: -5px;
}

@keyframes animloader {
  0% {
    box-shadow: 10px 0 rgba(255, 255, 255, 0), 20px 0 rgba(255, 255, 255, 0);
  }

  50% {
    box-shadow: 10px 0 #000, 20px 0 rgba(255, 255, 255, 0);
  }

  100% {
    box-shadow: 10px 0 #000, 20px 0 #000;
  }
}

#breed-select {
  display: none;
}

.ss-main {
  width: 300px;
}

.cat-info {
  overflow: hidden;
}

.cat-info img {
  float: left;
  max-width: 300px;
  height: auto;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 10px 20px 10px 0;
  box-shadow: 0 0 5px rgba(0, 0, 0, .3);
}

.cat-info p {
  margin-bottom: 8px;
  font-size: 16px;
}

.cat-info p strong {
  color: #ff4500;
  font-weight: bold;
}

